import { RestArbeidssker, Status } from './Rest';
import { medCookies } from './api';

export interface Arbeidssker {
    harCv: boolean;
    harJobbprofil: boolean;
}

export const hentArbeidssker = async (aktrid: string): Promise<RestArbeidssker> => {
    try {
        const respons = await fetch(`/finn-kandidat-api/synlighet/${aktrid}`, medCookies);

        if (!respons.ok || respons.status === 204) {
            return {
                status: Status.Feil,
                statusKode: respons.status,
            };
        }

        return {
            status: Status.Suksess,
            data: await respons.json(),
        };
    } catch (error) {
        return {
            status: Status.UkjentFeil,
        };
    }
};
