import React, { FunctionComponent } from 'react';
import { Alert, BodyShort } from '@navikt/ds-react';
import { RestArbeidssker, Samtykkestatus, Status } from '../api/Rest';
import css from './Advarsel.module.css';

enum Variant {
    IngenJobbprofil = 'Kandidaten har ikke jobbprofil',
    IngenCv = 'Kandidaten har ikke CV',
    ManglerSamtykke = 'Kandidaten har ikke blitt informert om NAVs behandlingsgrunnlag',
}

interface Props {
    arbeidssker: RestArbeidssker;
    samtykke: Samtykkestatus;
}

const Advarsel: FunctionComponent<Props> = ({ arbeidssker, samtykke }) => {
    if (
        arbeidssker.status === Status.Feil &&
        arbeidssker.statusKode === 403 &&
        samtykke === Status.Suksess
    ) {
        return (
            <Alert variant="warning">Du har ikke tilgang til  se informasjon om brukeren.</Alert>
        );
    }

    let advarsel = [];

    if (arbeidssker.status === Status.Suksess) {
        if (!arbeidssker.data.harCv) {
            advarsel.push(Variant.IngenCv);
        }
        if (!arbeidssker.data.harJobbprofil) {
            advarsel.push(Variant.IngenJobbprofil);
        }
    }

    if (samtykke === Status.IkkeFunnet) {
        advarsel.push(Variant.ManglerSamtykke);
    }

    if (advarsel.length === 0) {
        return null;
    } else {
        return (
            <Alert variant="warning" className={css.advarsel}>
                <BodyShort>
                    Brukeren er ikke synlig i kandidatsket i Rekrutteringsbistand.
                </BodyShort>
                <BodyShort>rsak er:</BodyShort>
                <ul className={css.liste}>
                    {advarsel.map((melding) => (
                        <BodyShort as="li" key={melding}>
                            {melding}
                        </BodyShort>
                    ))}
                </ul>
                {advarsel.includes(Variant.ManglerSamtykke) && (
                    <>
                        <BodyShort>Be brukeren om :</BodyShort>
                        <ol className={css.liste}>
                            <BodyShort as="li">logge inn p arbeidsplassen.no</BodyShort>
                            <BodyShort as="li">
                                lese teksten om at du m dele CV-en med NAV
                            </BodyShort>
                            <BodyShort as="li">
                                g videre og gjennomfre det tjenesten ber om
                            </BodyShort>
                        </ol>
                    </>
                )}
            </Alert>
        );
    }
};

export default Advarsel;
